	module.exports = {"data":{"site":{"siteMetadata":{"title":"Shang-Yu Su","subtitle":"PhD candidate at National Taiwan University, research interests cover Natural Language Processing, and Dialogue Systems.","copyright":"© All rights reserved.","author":{"name":"Shang-Yu Su","twitter":"#"},"disqusShortname":"","url":"https://www.shangyusu.com"}},"markdownRemark":{"id":"/Users/ShangYu/Desktop/github/shangyusu.github.io/src/pages/articles/ACL2020-JDL/index.md absPath of file >>> MarkdownRemark","html":"<p>Full paper:\n<a href=\"/acl2020-b9252510ccde72c8db3c727a50032442.pdf\" target=\"_blank\">Here</a>,\n<a href=\"https://arxiv.org/abs/2004.14710\" target=\"_blank\">arXiv</a></p>\n<p><b>This paper is published in Proceedings of The 58th Annual Meeting of the Association for Computational Linguistics (ACL 2020).</b></p>\n<p>In modular dialogue systems, natural language understanding (NLU) and natural language generation (NLG) are two critical components, where NLU extracts the semantics from the given texts and NLG is to construct corresponding natural language sentences based on the input semantic representations.\nHowever, the dual property between understanding and generation has been rarely explored.\nThe prior work is the first attempt that utilized the duality between NLU and NLG to improve the performance via a dual supervised learning framework.\nHowever, the prior work still learned both components in <em>supervised</em> manner, instead, this paper introduces a general learning framework to effectively exploit such duality, providing flexibility of incorporating both <em>supervised</em> and <em>unsupervised</em> learning algorithms to train language understanding and generation models in a joint fashion.\nThe benchmark experiments demonstrate that the proposed approach is capable of boosting the performance of both NLU and NLG.</p>","fields":{"tagSlugs":["/tags/nlu/","/tags/nlg/","/tags/dialogue/"]},"frontmatter":{"title":"Towards Unsupervised Language Understanding and Generation by Joint Dual Learning","tags":["NLU","NLG","Dialogue"],"date":"2020-04-03T22:40:32.169Z","description":"Shang-Yu Su, Chao-Wei Huang, and Yun-Nung Chen","conference":"ACL2020"}}},"pathContext":{"slug":"/posts/acl2020-jdl/"}}

